/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.valinux.com\">VA Linux</a> opened NASDAQ trading today with a bang... but I don't know if I made it on the train or not.</p>\n<p>As a \"Debian\" developer, I received 'the letter', returned the paperwork, and received confirmation that my paperwork was received. However, German applicants this week received notice that due to problems with German securities regulations, German residents were not eligible to participate in the IPO.</p>\n<p>However, on my paperwork I indicated that I am an American citizen, and I never received that notice. In fact, I have heard nothing about my application other than the notice that it had been received. However, as soon as I heard the price was set at $30, I sent in a confirmation of my willingness to buy at this price.</p>\n<p>I called VA Linux this evening, and they could not find my name on any of their lists. But they are going to call me back.</p>\n<p>With Red Hat, I also didn't know until a couple of days later that I had actually received my shares. We'll see what happens this time.</p>\n<p><i>I just noticed that this site is No. 100 on the editthispage.com <a href=\"http://www.editthispage.com/SiteRankings\">site rankings</a>, out of over 300 sites. I find it somewhat hard to believe that it is more interesting that over half of the other sites! The only advertising I've done was an ad on the weblog monitor... only because I wanted to remove the <a href=\"http://www.mausnews.de\">Maus Newsletter</a>, but there's no option to remove an ad... only to change the URL and the graphic!</i></p>","fields":{"title":"Linux fever...","date":"09 Dec 1999","url":"https://papascott.de/1999-12-09-linux-fever/"}}},"pathContext":{"slug":"/1999-12-09-linux-fever/","prev":{"fields":{"title":"Ready and Waiting...","date":"07 Dec 1999","slug":"/1999-12-07-ready-and-waiting/"}},"next":{"fields":{"title":"Friday misc...","date":"10 Dec 1999","slug":"/1999-12-10-friday-misc/"}}}}

/***/ })

